export default "/* container for stats */\r\n.caniuse-stats {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  justify-content: flex-start;\r\n  align-items: baseline;\r\n  cursor: pointer;\r\n}\r\n\r\nbutton.caniuse-cell {\r\n  margin: 1px 1px 0 0;\r\n  border: none;\r\n}\r\n\r\n.caniuse-browser {\r\n  position: relative;\r\n}\r\n\r\n/* handle case when printing */\r\n@media print {\r\n  .caniuse-cell.y::before {\r\n    content: \"✔️\";\r\n    padding: 0.5em;\r\n  }\r\n\r\n  .caniuse-cell.n::before{\r\n    content: \"❌\";\r\n    padding: 0.5em;\r\n  }\r\n\r\n  .caniuse-cell.d::before,\r\n  .caniuse-cell.a::before,\r\n  .caniuse-cell.x::before,\r\n  .caniuse-cell.p::before {\r\n    content: \"⚠️\";\r\n    padding: 0.5em;\r\n  }\r\n}\r\n\r\n/* reset styles, hide old versions by default */\r\n.caniuse-browser ul {\r\n  display: none;\r\n  margin: 0;\r\n  padding: 0;\r\n  list-style: none;\r\n  position: absolute;\r\n  left: 0;\r\n  z-index: 2;\r\n  background: #fff;\r\n  margin-top: 1px;\r\n}\r\n\r\n.caniuse-stats a {\r\n  white-space: nowrap;\r\n  align-self: center;\r\n  margin-left: .5em;\r\n}\r\n\r\n/* a browser version */\r\n.caniuse-cell {\r\n  display: flex;\r\n  color: rgba(0, 0, 0, 0.8);\r\n  font-size: 90%;\r\n  height: 0.8cm;\r\n  margin-right: 1px;\r\n  margin-top: 0;\r\n  min-width: 3cm;\r\n  overflow: visible;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\nli.caniuse-cell {\r\n  margin-bottom: 1px;\r\n}\r\n\r\n.caniuse-cell:focus {\r\n  outline: none;\r\n}\r\n\r\n.caniuse-cell:hover {\r\n  color: rgba(0, 0, 0, 1);\r\n}\r\n\r\n/* supports */\r\n.caniuse-cell.y {\r\n  background: #8bc34a;\r\n}\r\n\r\n/* no support */\r\n.caniuse-cell.n {\r\n  background: #e53935;\r\n}\r\n\r\n/* not supported by default / partial support etc\r\nsee https://github.com/Fyrd/caniuse/blob/master/CONTRIBUTING.md for stats */\r\n.caniuse-cell.d,\r\n.caniuse-cell.a,\r\n.caniuse-cell.x,\r\n.caniuse-cell.p {\r\n  background: #ffc107;\r\n}\r\n\r\n/* show rest of the browser versions */\r\n.caniuse-stats button:focus + ul,\r\n.caniuse-stats .caniuse-browser:hover > ul {\r\n  display: block;\r\n}\r\n"